function getCoverImage(type, id) {
  // 
  const typeImageMap = {
    'hot': 'images/a1.jpg',       // 
    'limited': 'images/a2.jpg',    // 
    'new_user': 'images/a3.jpeg',  // 
    'points': 'images/a4.jpg',     // 
    'vip': 'images/a5.jpeg',       // 
    'festival': '/images/banner2.jpg' // 
  }
  
  // 
  return typeImageMap[type] || localImages[id % localImages.length]
}

// 
function getTagsByType(type) {
  const tagMap = {
    'hot': ['', '', ''],
    'limited': ['', '', ''],
    'new_user': ['', '', ''],
    'points': ['', '', ''],
    'vip': ['', '', ''],
    'festival': ['', '', '']
  }
  return tagMap[type] || ['']
}
