    updateActivity: (id, data) => {
      return new Promise(resolve => {
        setTimeout(() => {
          // 
          // 
          const activityIndex = allActivities.findIndex(a => a.id == id)
          if (activityIndex !== -1) {
            // 
            allActivities[activityIndex] = {
              ...allActivities[activityIndex],
              ...data,
              updatedAt: new Date().toISOString().replace('T', ' ').substring(0, 19)
            }
          }
          
          resolve({
            code: 200,
            data: { id, ...data },
            message: ""
          })
        }, 500)
      })
    }
