<!-- src/components/Activity/ActivityDetail.vue -->
<template>
  <div class="activity-detail" v-if="activity">
    <div class="detail-toolbar" v-if="showDetailGuide">
      <el-button 
        type="warning" 
        size="small" 
        icon="el-icon-guide" 
        @click="startDetailGuide"
        class="detail-guide-btn"
      >
        
      </el-button>
    </div>
    <!--  -->
    <template v-if="editable && isEditing">
      <el-form ref="editForm" :model="editForm" :rules="formRules" label-width="100px">
        <!--  -->
        <el-card class="form-section">
          <template #header>
            <div class="section-header">
              <i class="el-icon-edit"></i>
              <span></span>
            </div>
          </template>
          
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="title">
                <el-input v-model="editForm.title" placeholder="" />
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="status">
                <el-select v-model="editForm.status" placeholder="" style="width: 100%;">
                  <el-option label="" value="active" />
                  <el-option label="" value="upcoming" />
                  <el-option label="" value="ended" />
                  <el-option label="" value="offline" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="type">
                <el-select v-model="editForm.type" placeholder="" style="width: 100%;">
                  <el-option label="" value="hot" />
                  <el-option label="" value="limited" />
                  <el-option label="" value="new_user" />
                  <el-option label="" value="points" />
                  <el-option label="" value="vip" />
                  <el-option label="" value="festival" />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="creator">
                <el-input v-model="editForm.creator" placeholder="" />
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-form-item label="" prop="description">
            <el-input
              v-model="editForm.description"
              type="textarea"
              :rows="3"
              placeholder=""
              maxlength="500"
              show-word-limit
            />
          </el-form-item>
        </el-card>
        
        <!--  -->
        <el-card class="form-section">
          <template #header>
            <div class="section-header">
              <i class="el-icon-time"></i>
              <span></span>
            </div>
          </template>
          
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="startTime">
                <el-date-picker
                  v-model="editForm.startTime"
                  type="datetime"
                  placeholder=""
                  style="width: 100%;"
                  value-format="yyyy-MM-dd HH:mm:ss"
                />
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="endTime">
                <el-date-picker
                  v-model="editForm.endTime"
                  type="datetime"
                  placeholder=""
                  style="width: 100%;"
                  value-format="yyyy-MM-dd HH:mm:ss"
                />
              </el-form-item>
            </el-col>
          </el-row>
        </el-card>
        
        <!--  -->
        <el-card class="form-section">
          <template #header>
            <div class="section-header">
              <i class="el-icon-s-data"></i>
              <span></span>
            </div>
          </template>
          
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="">
                <el-input-number
                  v-model="editForm.participants"
                  :min="0"
                  :step="100"
                  style="width: 100%;"
                />
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="">
                <el-input-number
                  v-model="editForm.progress"
                  :min="0"
                  :max="100"
                  :step="1"
                  style="width: 100%;"
                />
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-form-item label="" prop="rules">
            <el-input
              v-model="editForm.rules"
              type="textarea"
              :rows="4"
              placeholder=""
            />
          </el-form-item>
        </el-card>
      </el-form>
      
      <!--  -->
      <div class="action-buttons">
        <el-button @click="cancelEdit"></el-button>
        <el-button type="primary" @click="submitEdit" :loading="submitting"></el-button>
      </div>
    </template>
    
    <!--  -->
    <template v-else>
      <!--  -->
      <div class="action-toolbar" v-if="editable">
        <el-button type="primary" icon="el-icon-edit" @click="startEdit">
          
        </el-button>
      </div>
      
      <!--  -->
      <el-card class="info-card">
        <div class="info-header">
          <div class="info-title">
            <h2>{{ activity.title }}</h2>
            <div class="info-tags">
              <el-tag :type="getTagType(activity.type)" size="medium">
                {{ getTypeName(activity.type) }}
              </el-tag>
              <el-tag :type="getStatusType(activity.status)" size="medium">
                {{ getStatusText(activity.status) }}
              </el-tag>
            </div>
          </div>
          <div class="info-meta">
            <span class="meta-item"><i class="el-icon-user"></i> {{ activity.creator }}</span>
            <span class="meta-item"><i class="el-icon-time"></i> {{ formatDate(activity.createdAt) }}</span>
          </div>
        </div>
        
        <el-divider></el-divider>
        
        <!--  -->
        <el-descriptions :column="2" border>
          <el-descriptions-item label="ID">{{ activity.id }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatParticipants(activity.participants) }}</el-descriptions-item>
          <el-descriptions-item label="">{{ activity.views }}</el-descriptions-item>
          <el-descriptions-item label="">
            <el-progress :percentage="activity.progress" :status="getProgressStatus(activity.progress)" />
          </el-descriptions-item>
          <el-descriptions-item label="">{{ formatFullDate(activity.startTime) }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatFullDate(activity.endTime) }}</el-descriptions-item>
        </el-descriptions>
        
        <!--  -->
        <div class="description-section">
          <h4><i class="el-icon-info"></i> </h4>
          <p>{{ activity.description }}</p>
        </div>
      </el-card>
      
      <!--  -->
      <el-card class="rules-card">
        <template #header>
          <div class="card-header">
            <i class="el-icon-document"></i>
            <span></span>
          </div>
        </template>
        <div class="rules-content">
          <pre>{{ activity.rules }}</pre>
        </div>
      </el-card>
      
      <!--  -->
      <el-card class="data-card" v-if="activity.participantsData">
        <template #header>
          <div class="card-header">
            <i class="el-icon-s-data"></i>
            <span></span>
          </div>
        </template>
        
        <div class="data-content">
          <!--  -->
          <div class="data-item">
            <div class="data-label"></div>
            <div class="data-value">{{ formatParticipants(activity.participantsData.total) }}</div>
          </div>
          
          <!--  -->
          <div class="data-item">
            <h4></h4>
            <div class="daily-trend">
              <div 
                v-for="day in activity.participantsData.daily" 
                :key="day.date"
                class="trend-item"
              >
                <div class="trend-date">{{ day.date }}</div>
                <div class="trend-bar">
                  <div 
                    class="bar-fill" 
                    :style="{ height: Math.min(day.count / 50, 100) + '%' }"
                  ></div>
                </div>
                <div class="trend-count">{{ day.count }}</div>
              </div>
            </div>
          </div>
        </div>
      </el-card>
    </template>
      <GuideOverlay
      v-if="showDetailGuideDialog"
      :steps="detailGuideSteps"
      guide-key="activity-detail-guide"
      @complete="handleDetailGuideComplete"
      @skip="handleDetailGuideSkip"
    />
  </div>
</template>

<script>
import GuideOverlay from '@/components/Guide/GuideOverlay.vue'
export default {
  name: 'ActivityDetail',
  components: {
    GuideOverlay
  },
  props: {
    activity: {
      type: Object,
      required: true
    },
    editable: {
      type: Boolean,
      default: false
    },
    showDetailGuide: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
        showDetailGuideDialog: false,
      detailGuideSteps: this.getDetailGuideSteps(),
      isEditing: false,
      editForm: {},
      submitting: false,
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        status: [
          { required: true, message: '', trigger: 'change' }
        ],
        creator: [
          { required: true, message: '', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10, max: 500, message: ' 10  500 ', trigger: 'blur' }
        ],
        startTime: [
          { required: true, message: '', trigger: 'change' }
        ],
        endTime: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  watch: {
    activity: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          this.editForm = this.getEditFormData(newVal)
        }
      }
    }
  },
  methods: {
      getDetailGuideSteps() {
      const steps = [
        {
          title: '',
          content: '',
          order: 1
        }
      ]
      
      if (this.editable) {
        steps.push(
          {
            title: '',
            content: '""',
            order: 2
          },
          {
            title: '',
            content: '',
            order: 3
          },
          {
            title: '',
            content: '""',
            order: 4
          }
        )
      } else {
        steps.push(
          {
            title: '',
            content: '',
            order: 2
          },
          {
            title: '',
            content: '',
            order: 3
          },
          {
            title: '',
            content: '',
            order: 4
          },
          {
            title: '',
            content: '',
            order: 5
          },
          {
            title: '',
            content: '',
            order: 6
          },
          {
            title: '',
            content: '',
            order: 7
          }
        )
      }
      
      return steps
    },
    
    startDetailGuide() {
      this.showDetailGuideDialog = true
    },
    
    handleDetailGuideComplete(key) {
      this.showDetailGuideDialog = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.$message.success('')
    },
    
    handleDetailGuideSkip(key) {
      this.showDetailGuideDialog = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.$message.info('')
    },
    // 
    startEdit() {
      this.isEditing = true
      this.editForm = this.getEditFormData(this.activity)
    },
    
    // 
    cancelEdit() {
      this.isEditing = false
      this.editForm = this.getEditFormData(this.activity)
    },
    
    // 
    submitEdit() {
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          this.submitting = true
          
          // API
          setTimeout(() => {
            // 
            this.$emit('save', this.editForm)
            
            // 
            this.$message.success('')
            
            // 
            this.isEditing = false
            this.submitting = false
            
            // 
            Object.assign(this.activity, this.editForm)
          }, 800)
        } else {
          this.$message.warning('')
        }
      })
    },
    
    // 
    getEditFormData(activity) {
        setTimeout(() => {
        this.detailGuideSteps = this.getDetailGuideSteps()
      }, 100)
      return {
        id: activity.id,
        title: activity.title,
        type: activity.type,
        status: activity.status,
        creator: activity.creator,
        description: activity.description,
        startTime: activity.startTime,
        endTime: activity.endTime,
        participants: activity.participants,
        progress: activity.progress,
        rules: activity.rules || '1. \n2. \n3. \n4. \n5. '
      }
    },
    
    // 
    getTagType(type) {
      const map = {
        'hot': 'danger',
        'limited': 'warning',
        'new_user': 'success',
        'points': 'info',
        'vip': '',
        'festival': 'primary'
      }
      return map[type] || 'info'
    },
    
    getTypeName(type) {
      const map = {
        'hot': '',
        'limited': '',
        'new_user': '',
        'points': '',
        'vip': '',
        'festival': ''
      }
      return map[type] || ''
    },
    
    getStatusText(status) {
      const map = {
        'active': '',
        'upcoming': '',
        'ended': '',
        'offline': ''
      }
      return map[status] || ''
    },
    
    getStatusType(status) {
      const map = {
        'active': 'success',
        'upcoming': 'warning',
        'ended': 'info',
        'offline': 'danger'
      }
      return map[status] || 'info'
    },
    
    getProgressStatus(progress) {
      if (progress >= 80) return 'success'
      if (progress >= 50) return ''
      if (progress >= 30) return 'warning'
      return 'exception'
    },
    
    formatDate(date) {
      if (!date) return '-'
      return date.split(' ')[0]
    },
    
    formatFullDate(date) {
      if (!date) return '-'
      const d = new Date(date)
      return d.toLocaleString()
    },
    
    formatParticipants(num) {
      if (!num) return '0'
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + ''
      }
      return num.toString()
    }
  }
}
</script>

<style scoped>
/*  */
.form-section {
  margin-bottom: 20px;
}

.section-header {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
}

.section-header i {
  margin-right: 8px;
  color: #409EFF;
}

.action-buttons {
  text-align: right;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
}

.action-buttons .el-button {
  margin-left: 10px;
}

/*  */
.action-toolbar {
  margin-bottom: 20px;
  text-align: right;
}

.info-card {
  margin-bottom: 20px;
}

.info-header {
  margin-bottom: 20px;
}

.info-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.info-title h2 {
  margin: 0;
  font-size: 24px;
  color: #303133;
  flex: 1;
}

.info-tags {
  display: flex;
  gap: 10px;
  flex-shrink: 0;
}

.info-meta {
  display: flex;
  gap: 20px;
  font-size: 14px;
  color: #909399;
}

.meta-item {
  display: flex;
  align-items: center;
}

.meta-item i {
  margin-right: 5px;
}

.description-section {
  margin-top: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 4px;
}

.description-section h4 {
  margin: 0 0 10px 0;
  color: #409EFF;
  display: flex;
  align-items: center;
}

.description-section i {
  margin-right: 8px;
}

.description-section p {
  margin: 0;
  line-height: 1.6;
  color: #606266;
}

.rules-card,
.data-card {
  margin-bottom: 20px;
}

.card-header {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
}

.card-header i {
  margin-right: 8px;
  color: #409EFF;
}

.rules-content pre {
  margin: 0;
  font-family: inherit;
  white-space: pre-wrap;
  word-wrap: break-word;
  line-height: 1.6;
  color: #606266;
  font-size: 14px;
}

/*  */
.data-content {
  padding: 10px;
}

.data-item {
  margin-bottom: 30px;
}

.data-item:last-child {
  margin-bottom: 0;
}

.data-item h4 {
  margin: 0 0 15px 0;
  color: #303133;
}

.data-label {
  font-size: 14px;
  color: #909399;
  margin-bottom: 5px;
}

.data-value {
  font-size: 32px;
  font-weight: bold;
  color: #409EFF;
}

/*  */
.daily-trend {
  display: flex;
  align-items: flex-end;
  gap: 15px;
  height: 150px;
  padding: 20px 0;
}

.trend-item {
  flex: 1;
  text-align: center;
}

.trend-date {
  font-size: 12px;
  color: #909399;
  margin-bottom: 8px;
}

.trend-bar {
  height: 100px;
  background: #f5f7fa;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.bar-fill {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, #409EFF, #66b1ff);
  border-radius: 4px;
  transition: height 0.3s;
}

.trend-count {
  font-size: 12px;
  color: #303133;
  margin-top: 8px;
}
.detail-toolbar {
  text-align: right;
  margin-bottom: 20px;
}

.detail-guide-btn {
  background: linear-gradient(45deg, #9C27B0, #673AB7) !important;
  border-color: #9C27B0 !important;
  color: white !important;
}

.detail-guide-btn:hover {
  background: linear-gradient(45deg, #673AB7, #512DA8) !important;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(103, 58, 183, 0.4);
}
</style>