<!-- src/views/ActivityList.vue -->
<template>
  <div class="activity-list-page">
    <!--  -->
    <div class="page-header">
      <div class="header-left">
        <h1><i class="el-icon-s-order"></i> </h1>
        <el-button 
          type="text" 
          icon="el-icon-back" 
          @click="$router.push('/')"
          class="back-home-btn"
        >
          
        </el-button>
      </div>
      <div class="page-actions">
        <el-button type="primary" icon="el-icon-plus" @click="handleCreate" style="display:none;">
          
        </el-button>
        <el-button icon="el-icon-refresh" @click="refreshList">
          
        </el-button>
         <el-button 
          v-if="showHelpButton" 
          type="warning" 
          icon="el-icon-guide" 
          @click="startGuide"
        >
          
        </el-button>
      </div>
    </div>

    <!--  -->
    <el-card class="filter-card" shadow="never">
      <el-form :model="filterForm" inline label-width="80px">
        <!--  -->
        <el-form-item label="">
          <el-input
            v-model="filterForm.keyword"
            placeholder=""
            clearable
            @keyup.enter="handleSearch"
            @clear="handleSearch"
            style="width: 220px;"
          >
            <el-button slot="append" icon="el-icon-search" @click="handleSearch" />
          </el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select
            v-model="filterForm.status"
            placeholder=""
            clearable
            @change="handleSearch"
            style="width: 150px;"
          >
            <el-option label="" value="" />
            <el-option label="" value="active" />
            <el-option label="" value="upcoming" />
            <el-option label="" value="ended" />
            <el-option label="" value="offline" />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select
            v-model="filterForm.type"
            placeholder=""
            clearable
            @change="handleSearch"
            style="width: 150px;"
          >
            <el-option label="" value="" />
            <el-option label="" value="hot" />
            <el-option label="" value="limited" />
            <el-option label="" value="new_user" />
            <el-option label="" value="points" />
            <el-option label="" value="vip" />
            <el-option label="" value="festival" />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-date-picker
            v-model="filterForm.dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            @change="handleSearch"
            style="width: 280px;"
          />
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select
            v-model="filterForm.creator"
            placeholder=""
            clearable
            filterable
            @change="handleSearch"
            style="width: 150px;"
          >
            <el-option label="" value="" />
            <el-option label="" value="" />
            <el-option label="A" value="A" />
            <el-option label="B" value="B" />
            <el-option label="C" value="C" />
            <el-option label="D" value="D" />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item>
          <el-button type="primary" @click="handleSearch" :loading="loading">
            <i class="el-icon-search"></i> 
          </el-button>
          <el-button @click="handleReset">
            <i class="el-icon-refresh"></i> 
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!--  -->
    <GuideOverlay
      v-if="showGuide"
      :steps="guideSteps"
      guide-key="activity-list-guide"
      @complete="handleGuideComplete"
      @skip="handleGuideSkip"
    />
    <!--  -->
    <div class="stats-row" v-if="stats && !loading">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="stat-item total">
            <div class="stat-icon">
              <i class="el-icon-s-order"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.total }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item active">
            <div class="stat-icon">
              <i class="el-icon-success"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.active }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item upcoming">
            <div class="stat-icon">
              <i class="el-icon-time"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.upcoming }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item ended">
            <div class="stat-icon">
              <i class="el-icon-finished"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.ended }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <el-card class="list-card" shadow="never">
      <!--  -->
      <div class="list-toolbar">
        <div class="total-count">
           <span class="count-number">{{ total }}</span> 
          <span v-if="filterForm.keyword" class="keyword-hint">{{ filterForm.keyword }}</span>
        </div>
        <div class="toolbar-actions">
          <el-select v-model="pageSize" size="small" style="width: 120px;" @change="handlePageSizeChange">
            <el-option label="10" :value="10" />
            <el-option label="20" :value="20" />
            <el-option label="50" :value="50" />
          </el-select>
        </div>
      </div>

      <!--  -->
      <div v-if="loading && listData.length === 0" class="skeleton-container">
        <div v-for="i in 10" :key="i" class="skeleton-item">
          <el-skeleton animated :loading="true">
            <template slot="template">
              <div class="skeleton-content">
                <el-skeleton-item variant="image" style="width: 120px; height: 80px;" />
                <div style="flex: 1; margin-left: 15px;">
                  <el-skeleton-item variant="h3" style="width: 30%;" />
                  <el-skeleton-item variant="text" style="width: 60%; margin-top: 10px;" />
                  <div style="display: flex; margin-top: 15px;">
                    <el-skeleton-item variant="text" style="width: 80px; margin-right: 20px;" />
                    <el-skeleton-item variant="text" style="width: 100px;" />
                  </div>
                </div>
              </div>
            </template>
          </el-skeleton>
        </div>
      </div>

      <!--  -->
      <div v-if="listData.length > 0" class="list-container">
        <div 
          v-for="item in listData" 
          :key="item.id" 
          class="list-item" 
          @click="handleItemClick(item)"
        >
          <div class="item-image">
            <el-image
              :src="item.coverImage"
              fit="cover"
              style="width: 100%; height: 100%;"
            >
              <div slot="error" class="image-error">
                <i class="el-icon-picture-outline"></i>
              </div>
            </el-image>
          </div>
          <div class="item-content">
            <div class="item-header">
              <h4 class="item-title">{{ item.title }}</h4>
              <div class="item-meta">
                <el-tag :type="getStatusType(item.status)" size="mini">
                  {{ getStatusText(item.status) }}
                </el-tag>
                <span class="item-type">{{ getTypeName(item.type) }}</span>
              </div>
            </div>
            <p class="item-description">{{ item.description }}</p>
            <div class="item-footer">
              <div class="item-info">
                <span class="info-item">
                  <i class="el-icon-user"></i>
                  {{ item.creator }}
                </span>
                <span class="info-item">
                  <i class="el-icon-time"></i>
                  {{ formatDate(item.createdAt) }}
                </span>
                <span class="info-item">
                  <i class="el-icon-user-solid"></i>
                  {{ formatParticipants(item.participants) }} 
                </span>
              </div>
              <div class="item-actions">
                <el-button type="text" size="small" @click.stop="handleViewDetail(item)">
                  
                </el-button>
                <el-button
                  v-if="item.status !== 'ended'"
                  type="text"
                  size="small"
                  @click.stop="handleEdit(item)"
                >
                  
                </el-button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="!loading && listData.length === 0" class="empty-state">
        <el-empty description="">
          <el-button type="primary" @click="handleReset"></el-button>
        </el-empty>
      </div>

      <!--  -->
      <div v-if="total > 0" class="pagination-container">
        <el-pagination
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          :page-sizes="[10, 20, 50]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handlePageSizeChange"
          @current-change="handlePageChange"
          background
        />
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="detailDialogVisible"
      width="70%"
      top="5vh"
    >
      <ActivityDetail
        v-if="detailDialogVisible && selectedItem"
        :activity="selectedItem"
        :editable="false"
        show-detail-guide="true" 
        @close="detailDialogVisible = false"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="detailDialogVisible = false"></el-button>
        <el-button type="primary" @click="handleJoinActivity(selectedItem)">
          
        </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
  title=""
  :visible.sync="editDialogVisible"
  width="60%"
  top="5vh"
  @close="handleEditDialogClose"
>
  <ActivityDetail
    v-if="editDialogVisible && selectedItem"
    :activity="selectedItem"
    :editable="true"
    :show-detail-guide="true" 
    @save="handleSaveActivity"
    @close="editDialogVisible = false"
  />
</el-dialog>
  </div>
</template>

<script>
import ActivityAPI from '@/api/activity'
import ActivityDetail from '@/components/Activity/ActivityDetail.vue'
import GuideOverlay from '@/components/Guide/GuideOverlay.vue'

export default {
  name: 'ActivityList',
  components: {
    ActivityDetail,
     GuideOverlay
  },
  data() {
    return {
      // 
      filterForm: {
        keyword: '',
        status: '',
        type: '',
        dateRange: [],
        creator: ''
      },
      // 
      listData: [],
      // 
      currentPage: 1,
      pageSize: 20,
      total: 0,
      // 
      loading: false,
      // 
      stats: null,
      // 
      detailDialogVisible: false,
      editDialogVisible: false,
      selectedItem: null,
      // URL
      urlParams: {},
         showGuide: false,
      showHelpButton: true,
      guideSteps: [
        {
          title: '',
          content: '',
          order: 1
        },
        {
          title: '',
          content: '',
          order: 2
        },
        {
          title: '',
          content: '',
          order: 3
        },
        {
          title: '',
          content: '',
          order: 4
        },
        {
          title: '',
          content: '',
          order: 5
        },
        {
          title: '',
          content: '',
          order: 6
        },
        {
          title: '',
          content: '""',
          order: 7
        },
        {
          title: '',
          content: '',
          order: 8
        }
      ]
    }
  },
  watch: {
    // 
    '$route.query': {
      immediate: true,
      handler(newQuery) {
        this.updateFilterFromUrl(newQuery)
      }
    }
  },
  created() {
    this.initFromUrl()
    this.loadData()
    setTimeout(() => {
      this.checkGuide()
    }, 1000)
  },
  methods: {
        checkGuide() {
      // 
      const hasSeen = localStorage.getItem('guide_seen_activity-list-guide')
      if (!hasSeen) {
        // 
        setTimeout(() => {
          this.showGuide = true
        }, 1500)
      }
    },
    
    startGuide() {
      this.showGuide = true
    },
    
    handleGuideComplete(key) {
      this.showGuide = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.showHelpButton = false
      this.$message.success('')
    },
    
    handleGuideSkip(key) {
      this.showGuide = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.showHelpButton = false
      this.$message.info('')
    },
    // URL
    initFromUrl() {
      const query = this.$route.query
      this.filterForm = {
        keyword: query.keyword || '',
        status: query.status || '',
        type: query.type || '',
        dateRange: query.startDate && query.endDate ? [query.startDate, query.endDate] : [],
        creator: query.creator || ''
      }
      this.currentPage = parseInt(query.page) || 1
      this.pageSize = parseInt(query.pageSize) || 20
    },

    // URL
    updateUrl() {
      const params = {
        keyword: this.filterForm.keyword || undefined,
        status: this.filterForm.status || undefined,
        type: this.filterForm.type || undefined,
        creator: this.filterForm.creator || undefined,
        page: this.currentPage > 1 ? this.currentPage : undefined,
        pageSize: this.pageSize !== 20 ? this.pageSize : undefined
      }

      // 
      if (this.filterForm.dateRange && this.filterForm.dateRange.length === 2) {
        params.startDate = this.filterForm.dateRange[0]
        params.endDate = this.filterForm.dateRange[1]
      }

      // undefined
      Object.keys(params).forEach(key => {
        if (params[key] === undefined) {
          delete params[key]
        }
      })

      // URL
      this.$router.replace({
        query: params
      }).catch(() => {})
    },

    // URL
    updateFilterFromUrl(query) {
      // 
      if (JSON.stringify(this.urlParams) === JSON.stringify(query)) {
        return
      }
      
      this.urlParams = { ...query }
      
      this.filterForm = {
        keyword: query.keyword || '',
        status: query.status || '',
        type: query.type || '',
        dateRange: query.startDate && query.endDate ? [query.startDate, query.endDate] : [],
        creator: query.creator || ''
      }
      
      this.currentPage = parseInt(query.page) || 1
      this.pageSize = parseInt(query.pageSize) || 20
      
      // 
      this.loadData()
    },

    // 
    async loadData() {
      this.loading = true
      
      try {
        // 
        const params = {
          page: this.currentPage,
          pageSize: this.pageSize,
          keyword: this.filterForm.keyword,
          status: this.filterForm.status,
          type: this.filterForm.type,
          creator: this.filterForm.creator
        }

        // 
        if (this.filterForm.dateRange && this.filterForm.dateRange.length === 2) {
          params.startDate = this.filterForm.dateRange[0]
          params.endDate = this.filterForm.dateRange[1]
        }

        const res = await ActivityAPI.getActivityListWithPagination(params)
        
        if (res.code === 200) {
          const data = res.data
          this.listData = data.list || []
          this.total = data.total || 0
          this.stats = data.stats || null
          
          // URL
          this.updateUrl()
        } else {
          this.$message.error(res.message || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },

    // 
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 
    handleReset() {
      this.filterForm = {
        keyword: '',
        status: '',
        type: '',
        dateRange: [],
        creator: ''
      }
      this.currentPage = 1
      this.loadData()
    },

    // 
    handlePageChange(page) {
      this.currentPage = page
      this.loadData()
    },

    // 
    handlePageSizeChange(size) {
      this.pageSize = size
      this.currentPage = 1
      this.loadData()
    },

    // 
    handleItemClick(item) {
      this.selectedItem = item
      this.detailDialogVisible = true
    },

    // 
    handleViewDetail(item) {
      this.selectedItem = item
      this.detailDialogVisible = true
    },

    // 
    handleEdit(item) {
      this.selectedItem = item
      this.editDialogVisible = true
    },

    // 
    async handleSaveActivity(updatedData) {
    try {
      // 
      this.$message.loading('...', 0)
      
      // API
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // API
      // const res = await ActivityAPI.updateActivity(this.selectedItem.id, updatedData)
      
      // 
      const mockResponse = {
        code: 200,
        data: updatedData,
        message: ""
      }
      
      if (mockResponse.code === 200) {
        // 
        Object.assign(this.selectedItem, updatedData)
        
        // 
        const index = this.listData.findIndex(item => item.id === this.selectedItem.id)
        if (index !== -1) {
          this.$set(this.listData, index, { ...this.listData[index], ...updatedData })
        }
        
        // 
        this.$message.closeAll()
        this.$message.success(mockResponse.message)
        
        // 
        this.editDialogVisible = false
        
        // 
        this.selectedItem = null
      } else {
        this.$message.closeAll()
        this.$message.error(mockResponse.message || '')
      }
    } catch (error) {
      this.$message.closeAll()
      console.error(':', error)
      this.$message.error('')
    }
  },
  handleEditDialogClose() {
    this.selectedItem = null
  },

    // 
    handleJoinActivity(item) {
      this.$message.success(`${item.title}`)
      this.detailDialogVisible = false
      // 
      this.loadData()
    },

    // 
    refreshList() {
      this.loadData()
    },

    // 
    handleCreate() {
      this.$message.info('')
    },

    // 
    getTypeName(type) {
      const map = {
        'hot': '',
        'limited': '',
        'new_user': '',
        'points': '',
        'vip': '',
        'festival': ''
      }
      return map[type] || ''
    },

    getStatusText(status) {
      const map = {
        'active': '',
        'upcoming': '',
        'ended': '',
        'offline': ''
      }
      return map[status] || ''
    },

    getStatusType(status) {
      const map = {
        'active': 'success',
        'upcoming': 'warning',
        'ended': 'info',
        'offline': 'danger'
      }
      return map[status] || 'info'
    },

    formatDate(date) {
      if (!date) return '-'
      return date.split(' ')[0]
    },

    formatParticipants(num) {
      if (!num) return '0'
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + ''
      }
      return num.toString()
    }
  }
}
</script>

<style scoped>
.activity-list-page {
  padding: 20px;
  background: #f5f7fa;
  min-height: calc(100vh - 60px);
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 15px;
}

.header-left h1 {
  margin: 0;
  color: #303133;
  display: flex;
  align-items: center;
}

.header-left i {
  margin-right: 10px;
  font-size: 24px;
}

.back-home-btn {
  color: #409EFF;
}

.back-home-btn:hover {
  color: #66b1ff;
}

.filter-card {
  margin-bottom: 20px;
  border-radius: 8px;
}

/*  */
.stats-row {
  margin-bottom: 20px;
}

.stat-item {
  background: white;
  border-radius: 8px;
  padding: 20px;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  font-size: 24px;
  color: white;
}

.stat-item.total .stat-icon {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.stat-item.active .stat-icon {
  background: linear-gradient(135deg, #4caf50 0%, #2e7d32 100%);
}

.stat-item.upcoming .stat-icon {
  background: linear-gradient(135deg, #ff9800 0%, #f57c00 100%);
}

.stat-item.ended .stat-icon {
  background: linear-gradient(135deg, #9e9e9e 0%, #616161 100%);
}

.stat-content {
  flex: 1;
}

.stat-value {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 14px;
  color: #909399;
}

/*  */
.list-card {
  border-radius: 8px;
}

.list-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ebeef5;
}

.total-count {
  font-size: 16px;
  color: #303133;
}

.count-number {
  font-weight: bold;
  color: #409EFF;
  font-size: 20px;
  margin: 0 4px;
}

.keyword-hint {
  font-size: 14px;
  color: #909399;
  margin-left: 8px;
}

/*  */
.skeleton-container {
  padding: 10px 0;
}

.skeleton-item {
  margin-bottom: 15px;
  padding: 15px;
  background: white;
  border-radius: 4px;
  border: 1px solid #ebeef5;
}

.skeleton-content {
  display: flex;
}

/*  */
.list-container {
  min-height: 400px;
}

.list-item {
  display: flex;
  padding: 15px;
  margin-bottom: 10px;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  background: white;
}

.list-item:hover {
  border-color: #409EFF;
  box-shadow: 0 2px 12px rgba(64, 158, 255, 0.1);
  transform: translateY(-2px);
}

.item-image {
  width: 120px;
  height: 80px;
  border-radius: 4px;
  overflow: hidden;
  flex-shrink: 0;
  margin-right: 15px;
}

.item-content {
  flex: 1;
  min-width: 0;
}

.item-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.item-title {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex: 1;
}

.item-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
}

.item-type {
  font-size: 12px;
  color: #909399;
  padding: 2px 6px;
  background: #f4f4f5;
  border-radius: 2px;
}

.item-description {
  margin: 0 0 10px 0;
  font-size: 14px;
  color: #606266;
  overflow: hidden;
