<!-- src/views/ActivityManage.vue -->
<template>
  <div class="activity-manage">
    <!--  -->
    <div class="page-header">
      <h1></h1>
      <div class="page-actions">
        <!-- <el-button type="primary" icon="el-icon-plus" @click="handleCreate">
          
        </el-button> -->
        <el-button icon="el-icon-refresh" @click="handleRefreshData">
          
        </el-button>
        <!--  -->
        <el-button 
          v-if="showHelpButton" 
          type="warning" 
          icon="el-icon-guide" 
          @click="startGuide"
        >
          
        </el-button>
      </div>
    </div>
    
    <!--  -->
    <el-row :gutter="20" class="stats-row">
      <el-col :span="6">
        <el-card shadow="hover">
          <div class="stat-item">
            <div class="stat-icon" style="background: #e6f7ff;">
              <i class="el-icon-star-on" style="color: #1890ff;"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.totalActivities }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover">
          <div class="stat-item">
            <div class="stat-icon" style="background: #f6ffed;">
              <i class="el-icon-time" style="color: #52c41a;"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.activeActivities }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover">
          <div class="stat-item">
            <div class="stat-icon" style="background: #fff7e6;">
              <i class="el-icon-warning-outline" style="color: #fa8c16;"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ stats.upcomingActivities }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover">
          <div class="stat-item">
            <div class="stat-icon" style="background: #f9f0ff;">
              <i class="el-icon-user-solid" style="color: #722ed1;"></i>
            </div>
            <div class="stat-content">
              <div class="stat-value">{{ formatParticipants(stats.totalParticipants) }}</div>
              <div class="stat-label"></div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    
    <!-- Banner -->
    <ActivityBanner 
      v-if="pageData.banner" 
      :banner="pageData.banner"
      @banner-click="handleBannerClick"
    />
    
    <el-row :gutter="20">
      <!--  -->
      <el-col :span="6">
        <!--  -->
        <el-card shadow="hover" class="category-section">
          <template #header>
            <div class="section-header">
              <i class="el-icon-s-operation" style="margin-right: 8px;"></i>
              <span></span>
            </div>
          </template>
          <CategoryEntry
            v-for="category in pageData.categories"
            :key="category.id"
            :category="category"
            @category-click="handleCategoryClick"
          />
        </el-card>
        
        <!--  -->
        <Announcement
          :announcements="pageData.announcements"
          @announcement-click="handleAnnouncementClick"
        />
      </el-col>
      
      <!--  -->
      <el-col :span="18">
        <!--  -->
        <SwitchLayout
          :layout-type="layoutType"
          @layout-change="handleLayoutChange"
          @columns-change="handleColumnsChange"
          @sort-change="handleSortChange"
        />
        
        <!--  -->
        <div class="activity-display">
          <!--  -->
          <ActivityGrid
            v-if="layoutType === 'grid'"
            :activities="sortedActivities"
            :columns="columns"
            @card-click="handleActivityClick"
          />
          
          <!--  -->
          <ActivityCarousel
            v-else-if="layoutType === 'carousel'"
            :activities="sortedActivities"
            @card-click="handleActivityClick"
          />
          
          <!--  -->
          <el-table
            v-else-if="layoutType === 'list'"
            :data="sortedActivities"
            style="width: 100%"
          >
            <el-table-column prop="title" label="" width="200">
              <template slot-scope="{ row }">
                <div class="activity-title">
                  <el-image
                    :src="row.image"
                    style="width: 40px; height: 40px; border-radius: 4px; margin-right: 10px;"
                  ></el-image>
                  <span>{{ row.title }}</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="type" label="" width="120">
              <template slot-scope="{ row }">
                <el-tag :type="getTypeTag(row.type)">
                  {{ getTypeName(row.type) }}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="status" label="" width="100">
              <template slot-scope="{ row }">
                <el-tag :type="getStatusType(row.status)">
                  {{ row.status }}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="participants" label="" width="120">
              <template slot-scope="{ row }">
                {{ formatParticipants(row.participants) }}
              </template>
            </el-table-column>
            <el-table-column prop="progress" label="" width="150">
              <template slot-scope="{ row }">
                <el-progress 
                  :percentage="row.progress" 
                  :status="getProgressStatus(row.progress)"
                />
              </template>
            </el-table-column>
            <el-table-column label="" width="150">
              <template slot-scope="{ row }">
                <el-button type="text" @click="handleViewDetail(row)"></el-button>
                <el-button type="text" @click="handleEdit(row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    
    <!-- "" -->
    <el-card class="view-all-card" shadow="hover">
      <div class="view-all-content">
        <div class="view-all-info">
          <h3><i class="el-icon-s-order"></i> </h3>
          <p></p>
        </div>
        <div class="view-all-actions">
          <el-button type="primary" @click="goToList">
            <i class="el-icon-right"></i> 
          </el-button>
        </div>
      </div>
    </el-card>
    
    <!--  -->
    <el-dialog
      :title="selectedActivity ? selectedActivity.title : ''"
      :visible.sync="detailDialogVisible"
      width="60%"
    >
      <ActivityDetail :activity="selectedActivity" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="detailDialogVisible = false"></el-button>
        <el-button type="primary" @click="handleJoinActivity(selectedActivity)"></el-button>
      </span>
    </el-dialog>
    
    <!--  -->
    <GuideOverlay
      v-if="showGuide"
      :steps="guideSteps"
      guide-key="activity-home-guide"
      @complete="handleGuideComplete"
      @skip="handleGuideSkip"
    />
  </div>
</template>

<script>
import ActivityAPI from '@/api/activity'
import ActivityBanner from '@/components/Activity/ActivityBanner.vue'
import CategoryEntry from '@/components/Activity/CategoryEntry.vue'
import Announcement from '@/components/Activity/Announcement.vue'
import ActivityGrid from '@/components/Activity/ActivityGrid.vue'
import ActivityCarousel from '@/components/Activity/ActivityCarousel.vue'
import SwitchLayout from '@/components/Layout/SwitchLayout.vue'
import ActivityDetail from '@/components/Activity/ActivityDetail.vue'
import GuideOverlay from '@/components/Guide/GuideOverlay.vue'

export default {
  name: 'ActivityManage',
  components: {
    ActivityBanner,
    CategoryEntry,
    Announcement,
    ActivityGrid,
    ActivityCarousel,
    SwitchLayout,
    ActivityDetail,
    GuideOverlay
  },
  data() {
    return {
      pageData: {
        banner: null,
        categories: [],
        announcements: [],
        activities: []
      },
      layoutType: 'grid',
      columns: 3,
      sortBy: 'time',
      selectedActivity: null,
      detailDialogVisible: false,
      stats: {
        totalActivities: 0,
        activeActivities: 0,
        upcomingActivities: 0,
        totalParticipants: 0
      },
      // 
      showGuide: false,
      showHelpButton: true, // 
      guideSteps: [
        {
          title: '',
          content: '',
          order: 1
        },
        {
          title: '',
          content: '',
          order: 2
        },
        {
          title: 'Banner',
          content: 'Banner',
          order: 3
        },
        {
          title: '',
          content: '',
          order: 4
        },
        {
          title: '',
          content: '',
          order: 5
        },
        {
          title: '',
          content: '""',
          order: 6
        }
      ]
    }
  },
  computed: {
    sortedActivities() {
      const activities = [...this.pageData.activities]
      switch (this.sortBy) {
        case 'time':
          return activities.sort((a, b) => new Date(b.startTime) - new Date(a.startTime))
        case 'participants':
          return activities.sort((a, b) => b.participants - a.participants)
        case 'progress':
          return activities.sort((a, b) => b.progress - a.progress)
        default:
          return activities
      }
    }
  },
  created() {
    this.fetchData()
    // 
    setTimeout(() => {
      this.checkGuide()
    }, 1000)
  },
  methods: {
    // 
    async fetchData() {
      try {
        const res = await ActivityAPI.getActivityList()
        if (res.code === 200) {
          this.pageData = res.data
          this.calculateStats()
        }
      } catch (error) {
        this.$message.error('')
      }
    },
    
    // 
    calculateStats() {
      const activities = this.pageData.activities
      this.stats.totalActivities = activities.length
      this.stats.activeActivities = activities.filter(a => a.status === '' || a.status === 'active').length
      this.stats.upcomingActivities = activities.filter(a => a.status === '' || a.status === 'upcoming').length
      this.stats.totalParticipants = activities.reduce((sum, a) => sum + a.participants, 0)
    },
    
    // 
    handleRefreshData() {
      this.fetchData()
      this.$message.success('')
    },
    
    // 
    formatParticipants(num) {
      if (!num && num !== 0) return '0'
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + ''
      }
      return num.toString()
    },
    
    // 
    handleLayoutChange(type) {
      this.layoutType = type
    },
    
    // 
    handleColumnsChange(columns) {
      this.columns = columns
    },
    
    // 
    handleSortChange(sortBy) {
      this.sortBy = sortBy
    },
    
    // Banner
    handleBannerClick(banner) {
      this.$message.info(`Banner: ${banner.title}`)
    },
    
    // 
    handleCategoryClick(category) {
      this.$message.info(`: ${category.name}`)
    },
    
    // 
    handleAnnouncementClick(announcement) {
      this.$message.info(`: ${announcement.title}`)
    },
    
    // 
    handleActivityClick(activity) {
      this.selectedActivity = activity
      this.detailDialogVisible = true
    },
    
    // 
    handleViewDetail(activity) {
      this.selectedActivity = activity
      this.detailDialogVisible = true
    },
    
    // 
    handleEdit(activity) {
      this.$message.info(`: ${activity.title}`)
    },
    
    // 
    handleJoinActivity(activity) {
      if (activity) {
        this.$message.success(`${activity.title}`)
        this.detailDialogVisible = false
      }
    },
    
    // 
    handleCreate() {
      this.$message.info('')
    },
    
    // 
    goToList() {
      this.$router.push('/list')
    },
    
    // 
    getTypeTag(type) {
      const map = {
        'hot': 'danger',
        'limited': 'warning',
        'new_user': 'success',
        'points': 'info',
        'vip': '',
        'festival': 'primary'
      }
      return map[type] || 'info'
    },
    
    // 
    getTypeName(type) {
      const map = {
        'hot': '',
        'limited': '',
        'new_user': '',
        'points': '',
        'vip': '',
        'festival': ''
      }
      return map[type] || ''
    },
    
    // 
    getStatusType(status) {
      const map = {
        '': 'success',
        'active': 'success',
        '': 'warning',
        'upcoming': 'warning',
        '': 'info',
        'ended': 'info',
        '': 'danger',
        'offline': 'danger'
      }
      return map[status] || 'info'
    },
    
    // 
    getProgressStatus(progress) {
      if (progress >= 80) return 'success'
      if (progress >= 50) return ''
      if (progress >= 30) return 'warning'
      return 'exception'
    },
    
    // 
    
    // 
    checkGuide() {
      // 
      const hasSeen = localStorage.getItem('guide_seen_activity-home-guide')
      if (!hasSeen) {
        // 
        setTimeout(() => {
          this.showGuide = true
        }, 1500)
      }
    },
    
    // 
    startGuide() {
      this.showGuide = true
    },
    
    // 
    handleGuideComplete(key) {
      this.showGuide = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.showHelpButton = false
      this.$message.success('')
    },
    
    // 
    handleGuideSkip(key) {
      this.showGuide = false
      localStorage.setItem(`guide_seen_${key}`, 'true')
      this.showHelpButton = false
      this.$message.info('')
    }
  }
}
</script>

<style scoped>
.activity-manage {
  padding: 20px;
  background: #f5f7fa;
  min-height: 100vh;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.page-header h1 {
  margin: 0;
  color: #303133;
}

.stats-row {
  margin-bottom: 20px;
}

.stat-item {
  display: flex;
  align-items: center;
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
}

.stat-content {
  flex: 1;
}

.stat-value {
  font-size: 24px;
  font-weight: bold;
  color: #303133;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 14px;
  color: #909399;
}

.category-section {
  margin-bottom: 20px;
}

.section-header {
  font-size: 16px;
  font-weight: bold;
}

.activity-title {
  display: flex;
  align-items: center;
}

.activity-display {
  background: white;
  border-radius: 8px;
  padding: 20px;
}

/*  */
.view-all-card {
  margin-top: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.view-all-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.view-all-info h3 {
  margin: 0 0 10px 0;
  font-size: 20px;
}

.view-all-info p {
  margin: 0;
  opacity: 0.9;
}

.view-all-actions .el-button {
  background: white;
  color: #667eea;
  border: none;
}

.view-all-actions .el-button:hover {
  background: rgba(255, 255, 255, 0.9);
}
</style>